import axios from "axios";
// 
const loadSub = (params) => axios.get('/menu/sub', { params });
// 
const add = (formData) => axios.post('/menu', formData);
// 
const update = (id, formData) => axios.put(`/menu/${id}`, formData);
// 
const remove = (id, params) => axios.delete(`/menu/${id}`, { params });
// 
const loadTree = (params) => axios.get('/menu/tree', { params });
// icon
const setIcon = (id, formData) => axios.put(`/menu/icon/${id}`, formData);

export default {
    loadSub,
    add,
    update,
    remove,
    loadTree,
    setIcon,
}
