<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <el-alert title="Tips" type="warning" description="" show-icon
                :closable="false">
      </el-alert>
      <!--  -->
      <el-tree :data="treeMenu" ref="tree" :default-expanded-keys="defaultExpandedKeys" node-key="id"
               :props="defaultProps"
               class="am-margin-top-lg">
        <div class="node-box" slot-scope="{ node, data }">
          <div class="node-name">
            {{ node.label }}
          </div>
          <div class="node-action">
            <el-popover placement="left" class="am-margin-right-sm" trigger="hover">
              <i v-if="data.icon" class="am-text-xxl" :class="`el-icon-${data.icon}`"></i>
              <div v-else class="am-text-xxl"></div>
              <el-button slot="reference" type="text" :icon="data.icon?`el-icon-${data.icon}`:''" :disabled="data.id==1"
                         @click.stop="openIconModal(node,data)">
              </el-button>
            </el-popover>
            <el-button type="text" icon="el-icon-edit-outline" :disabled="data.id==1"
                       @click.stop="openEditModal(node,data)">
            </el-button>
            <el-button type="text" icon="el-icon-circle-plus-outline" @click.stop="openAddModal(node,data)">
              
            </el-button>
            <el-button type="text" icon="el-icon-delete" :disabled="data.id==1"
                       @click.stop="openRemoveModal(node,data)">
            </el-button>
            <div class="node-order am-margin-left-sm">
              
              <el-tag size="mini">{{ data.menu_order }}</el-tag>
            </div>
          </div>
        </div>
      </el-tree>
      <!-- Modal -->
      <el-dialog title="" :visible.sync="EditModalVisible" @closed="handleClosedDialog('editForm')">
        <el-form ref="editForm" :model="editForm" :rules="rules" label-width="80px" :label-position="'left'">
          <el-form-item label="" prop="name">
            <el-input v-model="editForm.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editForm.component" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editForm.path" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="menu_order">
            <el-input v-model="editForm.menu_order" placeholder="2001"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="EditModalVisible = false"> </el-button>
          <el-button type="primary" @click="handleEditMenu"> </el-button>
        </div>
      </el-dialog>
      <!-- Modal -->
      <el-dialog title="" width="72%" :visible.sync="IconModalVisible">
        <!--  -->
        <ul class="icon-list">
          <li v-for="item in icons" :key="item.id" :class="{checked:item.checked}" @click="handleCheckIcon(item)">
						<span>
							<i :class="`el-icon-${item.name}`"></i>
							<span class="icon-name">el-icon-{{ item.name }}</span>
						</span>
          </li>
        </ul>
        <!--  -->
        <el-pagination background layout="->,prev, pager, next" :page-size="pageSize" :total="iconTotal"
                       @current-change="handlePageChange"></el-pagination>
        <div slot="footer" class="dialog-footer">
          <el-button @click="IconModalVisible = false"> </el-button>
          <el-button type="primary" @click="handleUpdateIcon"> </el-button>
        </div>
      </el-dialog>
      <!-- Modal -->
      <el-dialog title="" :visible.sync="AddModalVisible" @closed="handleClosedDialog('addForm')">
        <el-form ref="addForm" :model="addForm" :rules="rules" label-width="80px" :label-position="'left'">
          <el-form-item label="" prop="name">
            <el-input v-model="addForm.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.component" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.path" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="menu_order">
            <el-input v-model="addForm.menu_order" placeholder="2001"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="AddModalVisible = false"> </el-button>
          <el-button type="primary" @click="handleAddMenu"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { Menu, Icon } from "@/api/index";

export default {
  name: "Menu",
  data() {
    return {
      defaultProps: { label: 'name' },
      // 
      IconModalVisible: false,
      icons: [],
      pageSize: 30,
      iconTotal: 0,
      checkedIcon: {
        icon_id: '',
        icon: ''
      },
      currentNode: '',
      //
      defaultExpandedKeys: [1],
      // Modal
      EditModalVisible: false,
      editForm: {
        id: '',
        name: '',
        pId: '',
        component: '',
        path: '',
        menu_order: '',
        icon: '',
        icon_id: '',
      },
      // Modal
      AddModalVisible: false,
      addForm: {
        name: "",
        pId: '',
        component: "",
        path: '',
        menu_order: '',
        icon: '',
        icon_id: '',
      },
      rules: {
        name: [{ required: true, type: 'string', message: '', trigger: 'blur' }],
        menu_order: [
          { required: true, type: 'string', message: '', trigger: 'blur' },
          { pattern: /^\d+$/, message: '', trigger: 'blur' }
        ],
      }
    }
  },
  created() {
    this.loadIcons(1);
    document.title = "";
  },
  computed: {
    ...mapGetters("Menu", ["treeMenu"]),
  },
  methods: {
    // element
    async loadIcons(pageIndex) {
      let { status, icons, total } = await Icon.list({ pageSize: this.pageSize, pageIndex });
      if (status) {
        icons.forEach((item) => {
          // --
          item.checked = false;
        });
        this.icons = icons;
        this.iconTotal = total;
      }
    },
    // 
    handlePageChange(pageIndex) {
      this.loadIcons(pageIndex);
    },
    // Modal
    openIconModal(node, data) {
      this.IconModalVisible = true;
      // node
      this.currentNode = node;
    },
    // 
    handleCheckIcon(data) {
      this.icons.forEach((item) => {
        // 
        item.checked = item === data ? !data.checked : false;
      });
      // /iconName
      this.checkedIcon.icon_id = data.checked ? data.id : '';
      this.checkedIcon.icon = data.checked ? data.name : '';
    },
    // 
    async handleUpdateIcon() {
      let { id } = this.currentNode.data;
      let { status, msg } = await this.$store.dispatch('Menu/SetIcon', { id, ...this.checkedIcon })
      if (status) {
        this.$message.success(msg);
        this.IconModalVisible = false;
      }
    },
    // 
    openRemoveModal(node, data) {
      this.$confirm(', ?', { type: 'warning', })
          .then(async () => {
            this.setDefaultExpandedKeys()
            let { status, msg } = await this.$store.dispatch('Menu/RemoveMenu', data.id);
            if (status) {
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          })
          .catch(() => {
            this.$message.info('');
          });
    },
    // ModalpId
    openAddModal(node, data) {
      this.addForm.pId = data.id;
      this.AddModalVisible = true;
      // data
      this.currentNode = node;
    },
    // 
    async handleAddMenu() {
      this.$refs.addForm.validate(async (valid) => {
        if (valid) {
          //
          this.setDefaultExpandedKeys();
          let { status, msg, data } = await this.$store.dispatch('Menu/CreateMenu', { ...this.addForm })
          if (!status) {
            this.$message.error(msg);
            return false;
          }
          this.$message.success(msg);
          this.AddModalVisible = false;
        }
      })
    },
    // Modal
    openEditModal(node, data) {
      this.EditModalVisible = true;
      //data
      this.editForm = { ...data };
      //childrenstate
      delete this.editForm.children;
      // node
      this.currentNode = node;
    },
    // 
    async handleEditMenu() {
      this.$refs.editForm.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await this.$store.dispatch('Menu/EditMenu', { ...this.editForm })
          if (!status) {
            this.$message.error(msg);
            return false;
          }
          this.$message.success(msg);
          this.EditModalVisible = false;
        }
      })
    },
    // dialog
    handleClosedDialog(formName) {
      this.$refs[formName].resetFields();
    },
    //
    setDefaultExpandedKeys() {
      // 
      let allNodes = this.$refs.tree.store._getAllNodes();
      let defaultExpandedNodes = [];
      allNodes.forEach(node => {
        node.expanded && defaultExpandedNodes.push(node.data.id);
      });
      this.defaultExpandedKeys = defaultExpandedNodes;
    },
  }
}
</script>

<style lang="less" scoped>
.node-box {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;

  .node-order {
    width: 100px;
    display: inline-block;
    font-size: 12px;
  }
}

ul.icon-list {
  overflow: hidden;
  list-style: none;
  padding: 0 !important;
  border: solid 1px #eaeefb;
  border-radius: 4px;
}

.icon-list li {
  float: left;
  width: 10%;
  text-align: center;
  height: 100px;
  line-height: 100px;
  color: #666;
  font-size: 10px;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin-right: -1px;
  margin-bottom: -1px;

  &::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
  }

  span {
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", SimSun, sans-serif;
    color: #99a9bf;
    transition: color 0.15s linear;
  }

  i {
    display: block;
    font-size: 26px;
    margin-bottom: 15px;
    color: #606266;
    transition: color 0.15s linear;
  }

  .icon-name {
    display: inline-block;
    padding: 0 3px;
    height: 1em;
  }

  &.checked,
  &.checked:hover {
    background-color: rgb(92, 182, 255);

    span,
    i {
      color: white;
    }
  }

  &:hover {

    span,
    i {
      color: rgb(92, 182, 255);
    }
  }
}
</style>
