import axios from 'axios';
//
const release = (formData) => axios.post('/admin/goods', formData);
//
const remove = (id, params) => axios.delete(`/admin/goods/${id}`, { params });
//
const update = (id, formData) => axios.put(`/admin/goods/${id}`, formData);
//
const list = (params) => axios.get('/admin/goods/list', { params });
//
const detail = (params) => axios.get('/admin/goods', { params });

export default {
    release,
    remove,
    update,
    list,
    detail,
}