import axios from 'axios';
//
const list = (params) => axios.get('/role/list', { params });
//
const add = (formData) => axios.post('/role', formData);
//
const remove = (id, params) => axios.delete(`/role/${id}`, { params });
//
const update = (id, formData) => axios.put(`/role/${id}`, formData);
//id
const loadConfig = (params) => axios.get('/role/config', { params });
//
const createMenu = (formData) => axios.post('/role/menu', formData);
//
const removeMenu = (id, params) => axios.delete(`/role/menu/${id}`, { params });

export default {
    list,
    add,
    remove,
    update,
    loadConfig,
    createMenu,
    removeMenu,
}